'use client'
import Image from 'next/image'
import style from '../CSS/userSide/products.module.css'
export default function Products({ products }) {
    return (
        <div className={style.productContainer}>
            <div className={style.productGrid}>
                {
                    products.length > 0 &&
                    products.map((product, index) => (
                        <div key={index} className={style.product}>
                            <div className={style.productImageDiv}>
                                {
                                    product.imageUrls && product.imageUrls.length > 0 && product.imageUrls[0] ?(
                                        <img
                                    src={product.imageUrls[0]}
                                    alt={product.productName}
                                    width={100}
                                    height={100}
                                    
                                    className={style.productImage}
                                />
                                    ):(
                                        <p>No Image</p>
                                    )
                                }
                            
                            </div>
                            <div className={style.category}>
                                Category
                            </div>
                            <div className={style.productName}>
                                {product.productName}
                            </div>
                            <p className={style.description}>
                                {product.description}
                            </p>

                            {
                                product.discount > 0 ? (
                                    <div className={style.discountPrice}>
                                        <p className={style.price}>
                                            Rs {product.price}
                                        </p>
                                        <p className={style.discount}>
                                            Rs {product.price - product.discount}
                                        </p>
                                    </div>
                                ) : (
                                    <div className={style.priceView}>
                                        <p className={style.price}>
                                            Rs {product.price}
                                        </p>
                                    </div>
                                )
                            }

                            <div>
                                <button type='button'>
                                    Add to Cart
                                </button>
                            </div>
                        </div>
                    )
                    )
                }
            </div>

        </div>
    )
}