"use client"

import { logout } from "../services/UserServices";
import style from '../CSS/userSide/navbar.module.css';
import { usePathname, useRouter } from "next/navigation";
import Image from "next/image";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons/faMagnifyingGlass";
import { faCartShopping, } from "@fortawesome/free-solid-svg-icons";
import { faClipboard, faHeart } from "@fortawesome/free-regular-svg-icons";
import { useNavigation } from "../Context/NavigationContext";
import Link from "next/link";
import { useEffect, useState } from "react";

export default function Navbar() {
    const [showUserBox,setShowUserBox] = useState(false);
    const { userData,loadCurrentUser} = useNavigation();
    const pathName = usePathname();
    const router = useRouter();
    const getActiveItem = () =>{
        if(pathName === "/")return "home";
        if(pathName === "/shop") return "shop";
        if(pathName === "/cartItems") return "cartItems";
        if(pathName === "/contact")return "contact";
    }
    const handleUserBox = () =>{
        setShowUserBox(prev => !prev);
    }
    useEffect(()=>{
        loadCurrentUser();
    },[])
    const activeNavItem = getActiveItem();
    return (
        <div className={style.container}>
            <div className={style.NavbarContainer}>
                <div className={style.title}>
                    <p>ShopEase</p>
                </div>
                <div className={style.searchGroup}>
                    <FontAwesomeIcon className={style.searchIcon} icon={faMagnifyingGlass} />
                    <input
                        name="productSearch"
                        placeholder="Looking for something?"
                        type="input"
                        className={style.searchInput}
                    />
                </div>
                <div className={style.navbarItems}>
                    <div className={style.navbarItem}>
                        <FontAwesomeIcon
                            className={style.favoriteIcon}
                            icon={faHeart}
                        />
                    </div>
                    <div className={style.navbarItem}>
                        <FontAwesomeIcon
                            className={style.orderIcon}
                            icon={faClipboard}
                        />
                    </div>
                    <div className={style.navbarItem}>
                        <FontAwesomeIcon
                            className={style.cartIcon}
                            icon={faCartShopping} />
                        {
                            <span className={style.cartMoney}>Rs 0.00 </span>
                        }
                    </div>

                    <div onClick={handleUserBox}  className={style.userImageSection}>
                        {
                            userData.profileImageUrl &&
                            <Image
                                src={userData.profileImageUrl}
                                alt={userData.username || "user"}
                                width={45}
                                height={45}
                                className={style.userImage}
                            />
                        }
                         {showUserBox && (
                        <div className={style.userBox}>
                            <p>Edit Profile</p>
                            <p onClick={()=> logout}>Logout</p>
                        </div>
                    )}
                    </div>
                   
                </div>
            </div>
            <div className={style.subNavbar}>
                <div className={style.subnavItems}>
                    <Link
                        href="/"
                        className={`${style.subnavItem} ${activeNavItem === 'home' ? style.active : ''}`}
                    >
                        Home
                    </Link>
                    <Link
                        href="/shop"
                        className={`${style.subnavItem} ${activeNavItem === 'shop' ? style.active : ''}`}
                    >
                        Shop
                    </Link>
                    <Link
                        href="/cartItems"
                        className={`${style.subnavItem} ${activeNavItem === 'cartItems' ? style.active : ''}`}
                    >
                        Cart Items
                    </Link>
                    <Link
                        href="/contact"
                        className={`${style.subnavItem} ${activeNavItem === 'contact' ? style.active : ''}`}
                    >
                        Contact
                    </Link>
                </div>
            </div>
        </div>
    )
}